*{
  font-family: Arial, Helvetica, sans-serif;

}
h2{
  text-align: center;
  padding-top: 20px;
}
.task-form{
  background-color:#3f674e;
}
.form{
    width: 40vw;
    display: flex;
    height: 73vh;
    flex-direction: column;
    margin-top: 80px;
    margin-left: auto;
    margin-right: auto; 
    padding:30px;
    border-radius: 1rem;
    padding-bottom: 20px;
    /* align-items: center; */
    justify-content: center;
}
.tasks{
  min-height: 12vh;
}


.text-task{
  width: 60%;
  padding-left: 20px;
  font-size: 0.9rem;
}
.task-view{
  width: 35%;
  display: flex;
  flex-direction: row;
  column-gap: 10px;
  row-gap: 5px;
  height: 40px;
  margin-top: -5px;
  margin-left: 40px;
}
.taskBtn{
  background-color: #264230;
  color: white;
  border-radius: 10px;
  text-align: center;
  width: 100px;
  outline: transparent;
  border: none;
  font-size: 0.8rem;
}

@media only screen and (max-width: 850px) {
  .form{
    width: 90vw;
    display: flex;
    height: 70vh;
    flex-direction: column;
    margin-top: 80px;
    margin-left: auto;
    margin-right: auto; 
    padding:30px;
    border-radius: 1rem;
    padding-bottom: 20px;
    justify-content: center;
}
.text-task{
  width: 30%;
  padding-left: 20px;
  font-size: 0.9rem;
}



