form > input{
    padding:.7em.7em ;
    border-radius:5PX;
    font-size: 16px;
    background-color:#F9FAF5;
    outline: transparent;
    border: none;
    margin-bottom: 10px;
   
}

form > label{

  font-weight: bold;
  color: #F9FAF5;
  font-size: medium;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: 10px !important;
margin-left: 5px;

}
.form-label{
  float: left ;
}
.sumbitBtn{
    margin-top: 10px;
    background-color:#f97e2d;
    color: whitesmoke;
    margin-left: auto;
    margin-right: auto;
    width: 10rem;
    padding: 10px;
    outline: transparent;
    border: none;
    font-weight: bold;
    font-size: larger;
}

.sumbitBtn:hover{
    background-color: #fe6700;
    color: antiquewhite;
    cursor: pointer;
}

.taskBtn:hover{
  background-color: #3f674e;
  transform:translateY(-2px);
}
